(function($) {
    /**
     * 
     * @param   modelName  
     * @param   customParams 
     */
    $.vpage.getMixParams = function(modelName, customParams) {
        var currentHistoryVpageParams = {
            vpage: modelName,
        }
        $.extend(true, currentHistoryVpageParams, customParams);
        return currentHistoryVpageParams;
    }
}(jQuery));